/**
 * Application Slice
 */

// Dependencies
import { createSlice, PayloadAction } from '@reduxjs/toolkit';

interface AppState {
	imageFile: null | string;
	imageName: string;
	imageModality: {
		value: string;
		label: string;
	};
	displayText: {
		dataContainer: string;
		imageContainer: string;
	};
	parsedData: string;
	loading: boolean;
}

const initialState: AppState = {
	imageFile: null,
	imageName: '',
	imageModality: {
		value: '',
		label: 'Select Modality',
	},
	displayText: {
		dataContainer: 'Upload Image to get data.',
		imageContainer: 'Upload image to get started.',
	},
	parsedData: '',
	loading: false,
};

const appSlice = createSlice({
	name: 'app',
	initialState,
	reducers: {
		setImage: (
			state,
			action: PayloadAction<{
				imageFile: string | null;
				imageName: string;
			}>
		) => {
			if (state.imageFile) URL.revokeObjectURL(state.imageFile);
			state.imageFile = action.payload.imageFile;
			state.imageName = action.payload.imageName;
		},
		setModality: (
			state,
			action: PayloadAction<{ value: string; label: string }>
		) => {
			state.imageModality = action.payload;
		},
		setLoading: (state, action: PayloadAction<boolean>) => {
			state.loading = action.payload;
		},
		reset: (state) => {
			state = initialState;
		},
	},
});

export const { reset, setModality, setLoading, setImage } = appSlice.actions;

export default appSlice.reducer;
