/**
 * Image kit Component
 */

// Dependencies
import React, { useRef } from 'react';
import { useAppSelector } from '../../../hooks/redux';
import './ImageKit.scss';

import UploadImage from './UploadImage';
import ZoomControls from './ZoomControls';
import UploadInput from './UploadInput';

import ProcessImagePng from '../../../assets/icons/execute.png';

const ImageKit = () => {
	const state = useAppSelector((state) => state.app);
	const imgRef = useRef<HTMLImageElement>(null);

	console.log(state);

	return (
		<div className='home__child imageKit__container'>
			<div className='imageKit__controls'>
				<p>
					{state.imageName} : {state.imageModality.label}
				</p>
				<button id='runModelBtn' title='Run model'>
					<img src={ProcessImagePng} alt='Execute' />
				</button>
			</div>
			<div className='imageKit__image'>
				{state.imageFile === null ? (
					<UploadImage />
				) : (
					<div id='imageKit__imageBox'>
						<img
							ref={imgRef}
							src={state.imageFile}
							key={state.imageFile}
							alt={state.imageName}
						/>
					</div>
				)}
			</div>
			<UploadInput />
			{state.imageFile !== null && <ZoomControls />}
		</div>
	);
};

export default ImageKit;
