/**
 * Upload Input Component
 */

import React from 'react';
import { useAppDispatch } from '../../../hooks/redux';
import { setImage } from '../../../store/features/app';

interface SetImagePayload {
	imageName: string;
	imageFile: any;
}

const UploadInput = () => {
	const dispatch = useAppDispatch();

	const handleImageChange = (e: React.ChangeEvent<HTMLInputElement>) => {
		const payload: SetImagePayload = {
			imageName: '',
			imageFile: null,
		};
		if (!e.target.files![0]) {
			dispatch(setImage(payload));
			return;
		}
		payload.imageFile = URL.createObjectURL(e.target.files![0]);
		payload.imageName = e.target.files![0].name;
		dispatch(setImage(payload));

		// imgRef.current!.src = state.imageFile;
	};

	return (
		<div className='imageKit__upload'>
			<label id='uploadImageBtn'>
				<input
					type='file'
					accept='image/*'
					onChange={handleImageChange}
				/>
				Upload Image
			</label>
		</div>
	);
};

export default UploadInput;
