/**
 * Data Control Components
 */

// Dependencies
import React from 'react';
import { useAppDispatch, useAppSelector } from '../../../hooks/redux';
import { setModality } from '../../../store/features/app';
import './DataControl.scss';

import GetAnimationData from './GetAnimationData';

const DataControl = () => {
	const state = useAppSelector((state) => state.app);
	const dispatch = useAppDispatch();

	const modalityOptions = [
		{
			value: '',
			label: 'Modality',
		},
		{
			value: 'ct-scan',
			label: 'CT Scan',
		},
		{
			value: 'mri',
			label: 'MRI',
		},
		{
			value: 'usg',
			label: 'USG',
		},
	];

	const handleSelectChange = (e: React.ChangeEvent<HTMLSelectElement>) => {
		const payload = {
			value: e.target.value,
			label: modalityOptions.find((ele) => ele.value === e.target.value)!
				.label,
		};
		dispatch(setModality(payload));
	};

	return (
		<div className='home__child dataControl__container'>
			<div className='dataControl__data'>
				<GetAnimationData text={'Upload Image to get data.'} />
			</div>
			<div className='dataControl__actions'>
				<select
					name='modality'
					value={state.imageModality.value}
					id='selectModality'
					onChange={handleSelectChange}
				>
					{modalityOptions.map((modality, index) => (
						<option value={modality.value} key={index}>
							{modality.label}
						</option>
					))}
				</select>
				<button id='printDataBtn'>Print Data</button>
			</div>
		</div>
	);
};

export default DataControl;
